//axios
import axios from 'axios';
//
import { Message } from 'element-ui';

//5s
axios.defaults.timeout = 5000;
//
axios.defaults.baseURL ='/api';


//http request 
axios.interceptors.request.use(
  config => {
    // const token = getCookie('');cookiejs-cookie
    config.data = JSON.stringify(config.data);
    config.headers = {
      'Content-Type':'application/x-www-form-urlencoded'
    }
    // if(token){
    //   config.params = {'token':token}
    // }
    return config;
  },
  error => {
    return Promise.reject(err);
  }
);


//http response 
axios.interceptors.response.use(
  response => {
    if(response.data.errCode ==2){
      router.push({
        path:"/",
        querry:{redirect:router.currentRoute.fullPath}//
      })
    }
    return response;
  },
  error => {
  	if (error && error.response) {
		switch (error.response.status) {
			case 400: error.message = '(400)' ; break;
			case 401: error.message = '(401)'; break;
			case 403: error.message = '(403)'; break;
			case 404: error.message = '(404)'; break;
			case 408: error.message = '(408)'; break;
			case 500: error.message = '(500)'; break;
			case 501: error.message = '(501)'; break;
			case 502: error.message = '(502)'; break;
			case 503: error.message = '(503)'; break;
			case 504: error.message = '(504)'; break;
			case 505: error.message = 'HTTP(505)'; break;
			default: error.message = `(${error.response.status})!`;
		}
	}else{
		error.message = '!'
	}
	Message.error(error.message)
    return Promise.reject(error)
  }
)


/**
 * get
 * @param url
 * @param data
 * @returns {Promise}
 */

export function fetch(url,params={}){
  return new Promise((resolve,reject) => {
    axios.get(url,{
      params:params
    })
    .then(response => {
    	console.log(response.data)
    	if(response.data == "timeout"){
    		alert("")
    	}
      resolve(response.data);
    })
    .catch(err => {
      reject(err)
    })
  })
}


/**
 * post
 * @param url
 * @param data
 * @returns {Promise}
 */

 export function post(url,data = {}){
   return new Promise((resolve,reject) => {
     axios.post(url,data)
          .then(response => {
            resolve(response.data);
          },err => {
            reject(err)
          })
   })
 }

 /**
 * patch
 * @param url
 * @param data
 * @returns {Promise}
 */

export function patch(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.patch(url,data)
         .then(response => {
           resolve(response.data);
         },err => {
           reject(err)
         })
  })
}

 /**
 * put
 * @param url
 * @param data
 * @returns {Promise}
 */

export function put(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.put(url,data)
         .then(response => {
           resolve(response.data);
         },err => {
           reject(err)
         })
  })
}


