<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div slot="header" class="clearfix">
		    <span></span>
		    <el-button style="float: right; padding: 3px 0" type="text"></el-button>
		  </div>
		  <div class="text item">
		    <el-form ref="form" :model="form" label-width="80px">
			    <el-form-item label="">
				    <el-select v-model="form.region" placeholder="">
				      <el-option label="" value="shanghai"></el-option>
				      <el-option label="" value="beijing"></el-option>
				    </el-select>
				</el-form-item>
			</el-form>
		  </div>
		</el-card>
		<el-row>
		  <el-col :span="24" style="text-align: center;">
		  	 <el-button type="danger"></el-button>
		  </el-col>
		</el-row>
		<el-card class="box-card box-card2">
		  <div class="text item" style="margin-bottom: 1rem;">
		  	
			FF0171123010AT83
			<el-button type="danger" style="float: right;" @click="open2"></el-button>
		  </div>
		  <div class="text item" style="margin-bottom: 1rem;"> :
			000000000001  
			<el-button type="danger" style="float: right;" @click="open3"></el-button>
		  </div>
			<div class="text item">/:
			9144030075253462XK  </div>
			<div class="text item">:
			<110,000.00  </div>
			<div class="text item">:
			2032-08-18  </div>
			<div class="text item">:
			  
			<el-button type="text" style="float: right;" round><router-link to="/assetman/first/edit"></router-link></el-button>
			</div>
		</el-card>
		
		<el-pagination
		  background
		  layout="prev, pager, next"
		  :total="30" class="pages">
		</el-pagination>
	</div>
	
</template>

<script>
	export default {
		data () {
			return {
				form: {
		          region: '',
		        }
			}
		},
		methods: {
	      open2() {
	        this.$confirm('?', '', {
	          confirmButtonText: '',
	          cancelButtonText: '',
	          type: 'warning'
	        }).then(() => {
	          this.$message({
	            type: 'success',
	            message: '!'
	          });
	        }).catch(() => {
	          this.$message({
	            type: 'info',
	            message: ''
	          });          
	        });
	      },
	      open3() {
	        this.$confirm('?', '', {
	          confirmButtonText: '',
	          cancelButtonText: '',
	          type: 'warning'
	        }).then(() => {
	          this.$message({
	            type: 'success',
	            message: '!'
	          });
	        }).catch(() => {
	          this.$message({
	            type: 'info',
	            message: ''
	          });          
	        });
	      }
	    }
	}
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
</style>


