<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div slot="header" class="clearfix">
		    <span></span>
		  </div>
		  <div class="text item" style="text-align: center;">
		    <el-button type="danger"></el-button>
		    <el-button type="danger"></el-button>
		    <el-button type="danger"></el-button>
		  </div>
		</el-card>
		
		<el-card class="box-card box-card2">
		  <div class="text item" style="margin-bottom: 20px;">
		  	
			FF0171123010AT83
			<el-button type="danger" style="float: right;" @click="open3"></el-button>
		  </div>
		  <div class="text item" style="margin-bottom: 20px;"> :
			000000000001  
		  </div>
			<div class="text item">/:
			9144030075253462XK  </div>
			<div class="text item">:
			<110,000.00  </div>
			<div class="text item">:
			2032-08-18  </div>
			<div class="text item">:
			  
			</div>
		</el-card>
		
		<el-pagination
		  background
		  layout="prev, pager, next"
		  :total="30" class="pages">
		</el-pagination>
	</div>
	
</template>

<script>
	export default {
		data () {
			return {
				form: {
		          region: '',
		        }
			}
		},
		methods: {
	      open3() {
	        this.$confirm('?', '', {
	          confirmButtonText: '',
	          cancelButtonText: '',
	          type: 'warning'
	        }).then(() => {
	          this.$message({
	            type: 'success',
	            message: '!'
	          });
	        }).catch(() => {
	          this.$message({
	            type: 'info',
	            message: ''
	          });          
	        });
	      }
	    }
	}
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
</style>


