<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div slot="header" class="clearfix">
		    <span></span>
		    <el-button style="float: right; padding: 3px 0" type="text">
		    	<router-link to="/debtman/first/add"></router-link>
		    </el-button>
		  </div>
		  <div class="text item">
		    <el-form ref="form" :model="form" label-width="80px">
			    <el-form-item label="">
				    <el-select v-model="form.region" placeholder="">
				      <el-option label="" value="shanghai"></el-option>
				      <el-option label="" value="beijing"></el-option>
				    </el-select>
				</el-form-item>
			</el-form>
		  </div>
		</el-card>
		
		<el-card class="box-card box-card2">
		  <div class="text item">
		  	
			FF0171123010AT83
		  </div>
		  <div class="text item"> :
			000000000001  </div>
			<div class="text item">/:
			9144030075253462XK  </div>
			<div class="text item">:
			<110,000.00  </div>
			<div class="text item">:
			2032-08-18  </div>
			<div class="text item">:
			  </div>
		</el-card>
		
		<el-pagination
		  background
		  layout="prev, pager, next"
		  :total="30" class="pages">
		</el-pagination>
	</div>
	
</template>

<script>
	export default {
		data () {
			return {
				form: {
		          region: '',
		        }
			}
		},
	}
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
</style>


