<script>
	export default {
		data () {
			return {
				input: '',
			}
		},
		methods: {
	      open3() {
	        this.$confirm('?', '', {
	          confirmButtonText: '',
	          cancelButtonText: '',
	          type: 'warning'
	        }).then(() => {
	          this.$message({
	            type: 'success',
	            message: '!'
	          });
	        }).catch(() => {
	          this.$message({
	            type: 'info',
	            message: ''
	          });          
	        });
	      }
	    }
	}
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
	.check-btn{
		border-radius: 60px;
    	padding: 8px;
    	float:right;
	}
	.transfer-btn{
		position: fixed;
		top: 45%;
		left: 0;
	}
	.el-badge__content.is-fixed{
		top: 4px;
		right: 16px;
		border: none;
	}
</style>


