<template>
	<div class="wrap">
		<el-card class="box-card">
		  <div class="text item">
		  	<div></div>
		  </div>
		</el-card>
		
		<el-form ref="form" :model="form" label-width="auto">
		  <el-form-item label="">
		    <el-input v-model="form.name"></el-input>
		  </el-form-item>
		  <el-form-item label="/">
		    <el-input v-model="form.name1"></el-input>
		  </el-form-item>
		  <el-form-item label="">
		    <el-input v-model="form.name2"></el-input>
		  </el-form-item>
		  <el-form-item label="">
		     <el-date-picker
		      v-model="value1"
		      type="date"
		      placeholder="">
		    </el-date-picker>
		    	
		    <el-date-picker
		      v-model="value2"
		      type="date"
		      placeholder="">
		    </el-date-picker>
		  </el-form-item>
		  <el-form-item label="">
		    <el-input v-model="form.name4"></el-input>
		  </el-form-item>
		  <el-form-item label="/">
		    <el-input v-model="form.name5"></el-input>
		  </el-form-item>
		  <el-form-item style="text-align: center;">
		    <el-button type="danger" @click="onSubmit"></el-button>
		  </el-form-item>
		</el-form>
		
	</div>
</template>
	
<script>
  export default {
    data() {
      return {
        form: {
          name: '',
          name1: '',
          name2: '',
          name4: '',
          name5: '',
        },
        value1: '',
        value2: '',
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      }
    }
  }
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
	div{
		margin: 0 auto;
	}
	.el-date-editor.el-input, .el-date-editor.el-input__inner{
		width: 100%;
	}
</style>


