		        }
			}
		},
		methods: {
	      open3() {
	        this.$confirm('?', '', {
	          confirmButtonText: '',
	          cancelButtonText: '',
	          type: 'warning'
	        }).then(() => {
	          this.$message({
	            type: 'success',
	            message: '!'
	          });
	        }).catch(() => {
	          this.$message({
	            type: 'info',
	            message: ''
	          });          
	        });
	      }
	    }
	}
</script>

<style>
	.item{
		text-align: center;
	}
	.box-card .item{
		text-align: left;
	}
	.pages{
		float: right;
		margin-top: 20px;
	}
	.el-message-box{
		width: 9rem;
	}
</style>


