     		that.$router.push('/besthe/shareholder')
     	},
     	previewAuthorLetter : function(){
     		var that = this
     		that.$router.push('/besthe/authorletter')
			},
			UpAuthorLetter : function(){
     		var that = this
     		that.$router.push('/besthe/upauthorletter')
     	},
     	goCreditReport : function(){
     		var that = this
     		that.$router.push('/besthe/creditreport')
			 },
			 goDataConfirm : function(){
     		var that = this
     		that.$router.push('/besthe/dataconfirm')
     	},
     	upVideoBtn : function(){
     		this.$refs.videoBox.dispatchEvent(new MouseEvent('click'))
     	},
	     handleClose(done) {
	        this.$confirm('')
	          .then(_ => {
	            this.dialogVisible = false
	          })
	          .catch(_ => {});
	      },
     	handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
	      },
	      beforeAvatarUpload(file) {
	        const isLt2M = file.size / 1024 / 1024 < 2;
	        if (!isLt2M) {
	          this.$message.error(' 2MB!');
	        }
	        return isLt2M;
	      },
	      handleAvatarError(res, file) {
	        console.log(res)
	        alert("")
	      },
     }
  }
