    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .avatar-uploader .el-upload{
  	width: 100%;
  }
	.el-checkbox{
		white-space:inherit
	}
	.widthAuto .el-checkbox__label{
		width:90%;
		vertical-align: middle;
	}
</style>


