<template>
	<div class="wrap">
		<el-card class="box-card0">
		  <div slot="header" class="clearfix">
		    <span></span>
		  </div>
		  <div class="changeUser">
		  	<div v-for="o in 2" :key="o" class="text item" style="text-align: left;">
		    	{{' ' + o }} <el-button style="float: right;" @click="setDefault(o)"></el-button>
		 	 </div>
		  </div>
		</el-card>
		<el-badge :value="4" class="item" style="display: inherit;margin:0.3rem auto;width: 16%;text-align: center;">
		  <el-button size="small"></el-button>
		</el-badge>
		
		<el-card class="box-card">
		<el-collapse accordion class="nav-list">
		  <el-collapse-item>
		    <template slot="title">
		      	
		    </template>
		    <div><router-link to="assetman"></router-link></div>
			<div><router-link to="debtman"></router-link></div>
			<div><router-link to="letsta"></router-link></div>
			<div><router-link to="elcacc"></router-link></div>
		  </el-collapse-item>
		  <el-collapse-item>
		    <template slot="title">
		      	
		    </template>
		    <div><router-link to="/seccenter"></router-link></div>
			<div><router-link to="/realname"></router-link></div>
			<div><router-link to="/rate"></router-link></div>
			<div><router-link to="/account"></router-link></div>
			<div><router-link to="/msg"></router-link></div>
			<div><router-link to="/invoiceman"></router-link></div>
			<div><router-link to="/besthe/login"></router-link></div>
		  </el-collapse-item>
		</el-collapse>
		</el-card>
		
	</div>
</template>
<script>
	export default {
		data () {
			return {
				titleMsg:''
			}
		},
		methods : {
			setDefault: function (o) {
				console.log(o)
	        },
		}
	}
</script>

<style>
	.wrap{
		text-align: left;
	}
  .text {
    font-size: 14px;
  }

  .box-card0 .item {
    margin-bottom: 1.0rem;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
  .nav-list div{
  	margin-bottom: 0;
  }
  .nav{
		display: block;
	}
</style>


